body {
  background: w;
  font-family: 'IBM Plex Sans Condensed', sans-serif;
  font-size: 1.75rem;
  text-align: center;
  min-height: 100vh;
  display: grid;
  place-items: center;
}

.App {
  background: white;
  padding: 4em;
  border-radius: 1em;
  box-shadow: 2px 2px 10px rgba(68, 68, 68, 0.575), 
              2px -2px 10px rgb(68, 68, 68, 0.575),
              -2px -2px 10px rgb(68, 68, 68, 0.575),
              -2px 2px 10px rgb(68, 68, 68, 0.575);
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
